.Hero {
  text-align: center;
}

.Hero-header {
  background-image: url("../img/nebula.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Hero-blurb {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.795);
  padding: 2.5%;
  border: 1px solid #fff;
  z-index: 100;
}

.Hero-title {
  font-weight: 500;
  font-size: 8vmin;
  margin-bottom: -2vmin;
}

.Hero-credit {
  font-size: 3vmin;
  margin-bottom: 10px;
}

.Hero-social {
  display: block;
}