.Usage {
  background-color: #000;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: space-around;
  border-top: 1px cyan solid;
  color: #fff;
}

.Usage-row {
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: space-around;
  margin-top: 3%;
  margin-bottom: 3%;
}

/*-- Table style --*/

#props {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
  color: #fff;
}

#props td,
#props th {
  border: 1px solid #ddd;
  padding: 8px;
}

#props tr:nth-child(even) {
  background-color: #303030;
}

#props th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #64ceaa;
  color: white;
}
